body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.keyur {
  background-image: url("https://images.unsplash.com/photo-1463797221720-6b07e6426c24?ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8Y29mZmVlJTIwc2hvcHxlbnwwfHwwfHw%3D&ixlib=rb-1.2.1&w=1000&q=80");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
}

.imagehome {
  background-image: url("https://res.cloudinary.com/trends-publishing/image/upload/dpr_1.0,g_center,w_760/f_auto,q_auto:best/cb_1220571504140671.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.test {
  flex-direction: column;
}


@-webkit-keyframes spinPizza {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes spinPizza {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.pizza {
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  -webkit-animation: spinPizza 5s linear infinite;
          animation: spinPizza 5s linear infinite;
}

.pizza:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}


@-webkit-keyframes moveBurger {
  0% { -webkit-transform: translateX(0); transform: translateX(0); }
  50% { -webkit-transform: translateX(50px); transform: translateX(50px); }
  100% { -webkit-transform: translateX(0); transform: translateX(0); }
}


@keyframes moveBurger {
  0% { -webkit-transform: translateX(0); transform: translateX(0); }
  50% { -webkit-transform: translateX(50px); transform: translateX(50px); }
  100% { -webkit-transform: translateX(0); transform: translateX(0); }
}

.burger {
  -webkit-animation: moveBurger 2s ease-in-out infinite;
          animation: moveBurger 2s ease-in-out infinite;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.burger:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.foodcard {
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.foodcard:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.hamburger {
  -webkit-animation: bounce 2s ease-in-out infinite;
          animation: bounce 2s ease-in-out infinite;
}

.fastfood {
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.fastfood:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.rating{
    position: absolute;
    top: 9px;
    right: 10px;
    color:white;
    background-color:#ae00ff; 
    padding: 10px;
}

.foodbutton {
  background-color: rgb(255, 170, 0); 
  color: white; 
  font-size: 20px; 
  padding: 13px 20px;
  border-radius: 5px; 
  text-align: center; 
  -webkit-text-decoration:wavy; 
          text-decoration:wavy; 
  transition: -webkit-transform 0.5s; 
  transition: transform 0.5s; 
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.foodbutton:hover {
  background-color: red; 
  cursor: pointer;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.breakfast-button {
  box-shadow: 2px 2px 2px #805300; 
}

.breakfast-button:hover {
  box-shadow: 4px 4px 4px #805300;
}
